'use client';

import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Progress } from '@/components/ui/progress';
import { useAuth } from '@/context/AuthContext';
import { useRouter } from 'next/navigation';
import { useEffect, useState } from 'react';
import { Category } from '../../types.d';

const DashboardLoading = () => (
  <div className="space-y-6">
    <Card>
      <CardHeader>
        <div className="h-8 bg-muted rounded w-1/2"></div>
      </CardHeader>
      <CardContent>
        <div className="h-10 bg-muted rounded w-full"></div>
      </CardContent>
    </Card>
    <Card>
      <CardHeader>
        <div className="h-8 bg-muted rounded w-1/3"></div>
      </CardHeader>
      <CardContent className="space-y-2">
        <div className="h-6 bg-muted rounded w-3/4"></div>
        <div className="h-6 bg-muted rounded w-1/2"></div>
        <div className="h-6 bg-muted rounded w-2/3"></div>
      </CardContent>
    </Card>
  </div>
);

export default function DashboardPage() {
  const { user, isAuthenticated, isLoading: isAuthLoading } = useAuth();
  const router = useRouter();
  const [totalQuestions, setTotalQuestions] = useState(0);
  const [isLoadingData, setIsLoadingData] = useState(true);

  useEffect(() => {
    // Protect the route
    if (!isAuthLoading && !isAuthenticated) {
      router.push('/');
    }
  }, [isAuthenticated, isAuthLoading, router]);

  useEffect(() => {
    // Fetch all questions to calculate total for progress bar
    const fetchTotalQuestions = async () => {
      try {
        const response = await fetch('http://localhost:5000/api/v1/content');
        const result = await response.json();
        const total = result.data.reduce(
          (acc: number, category: Category) => acc + category.questions.length,
          0
        );
        setTotalQuestions(total);
      } catch (error) {
        console.error('Failed to fetch total questions', error);
      } finally {
        setIsLoadingData(false);
      }
    };

    if (isAuthenticated) {
      fetchTotalQuestions();
    }
  }, [isAuthenticated]);

  if (isAuthLoading || isLoadingData) {
    return <DashboardLoading />;
  }

  if (!isAuthenticated || !user) {
    return null; // or a message telling them to log in
  }

  const completedCount = user.completedQuestions.length;
  const progressPercentage =
    totalQuestions > 0 ? (completedCount / totalQuestions) * 100 : 0;

  return (
    <main className="min-h-screen bg-background text-foreground p-4 sm:p-8">
      <div className="max-w-4xl mx-auto space-y-8">
        <header>
          <h1 className="text-4xl font-bold">Welcome, {user.name}</h1>
          <p className="text-muted-foreground">
            Here&apos; s a summary of your progress.
          </p>
        </header>

        <Card>
          <CardHeader>
            <CardTitle>Overall Progress</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="flex items-center gap-4">
              <Progress value={progressPercentage} className="w-full" />
              <span className="font-semibold">
                {Math.round(progressPercentage)}%
              </span>
            </div>
            <p className="text-muted-foreground mt-2">
              You have completed {completedCount} out of {totalQuestions}{' '}
              questions. Keep going!
            </p>
          </CardContent>
        </Card>

        {/* You can add the list of bookmarked questions here in a similar Card component later */}
      </div>
    </main>
  );
}
