'use client';

import {
  createContext,
  ReactNode,
  useContext,
  useEffect,
  useState,
} from 'react';
import { User } from '../types.d'; // Make sure Question is exported from your types

// Extend the User type to include progress
interface UserProfile extends User {
  completedQuestions: string[]; // Array of question IDs
  bookmarkedQuestions: string[];
}

interface AuthContextType {
  user: UserProfile | null;
  token: string | null;
  login: (userData: User, token: string) => void;
  logout: () => void;
  isAuthenticated: boolean;
  toggleQuestionProgress: (questionId: string) => Promise<void>;
  isLoading: boolean;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export const AuthProvider = ({ children }: { children: ReactNode }) => {
  const [user, setUser] = useState<UserProfile | null>(null);
  const [token, setToken] = useState<string | null>(null);
  const [isLoading, setIsLoading] = useState(true); // To handle initial auth check

  useEffect(() => {
    const storedToken = localStorage.getItem('token');
    const storedUser = localStorage.getItem('user');
    if (storedToken && storedUser) {
      setToken(storedToken);
      // Fetch the full user profile to get the latest progress
      fetchUserProfile(storedToken);
    } else {
      setIsLoading(false);
    }
  }, []);

  const fetchUserProfile = async (authToken: string) => {
    try {
      const response = await fetch(
        'http://localhost:5000/api/v1/user/profile',
        {
          headers: { Authorization: `Bearer ${authToken}` },
        }
      );
      if (!response.ok) throw new Error('Failed to fetch profile');
      const { data } = await response.json();
      setUser(data);
    } catch (error) {
      console.error(error);
      logout(); // If token is invalid, log the user out
    } finally {
      setIsLoading(false);
    }
  };

  const login = (userData: User, token: string) => {
    localStorage.setItem('user', JSON.stringify(userData));
    localStorage.setItem('token', token);
    setToken(token);
    // After login, fetch the full profile with progress data
    fetchUserProfile(token);
  };

  const logout = () => {
    setUser(null);
    setToken(null);
    localStorage.removeItem('user');
    localStorage.removeItem('token');
  };

  const toggleQuestionProgress = async (questionId: string) => {
    if (!token || !user) return;

    // Optimistic UI update: update the state immediately
    const isCompleted = user.completedQuestions.includes(questionId);
    const updatedCompletedQuestions = isCompleted
      ? user.completedQuestions.filter((id) => id !== questionId)
      : [...user.completedQuestions, questionId];

    setUser({ ...user, completedQuestions: updatedCompletedQuestions });

    try {
      const response = await fetch(
        'http://localhost:5000/api/v1/user/progress',
        {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            Authorization: `Bearer ${token}`,
          },
          body: JSON.stringify({ questionId }),
        }
      );

      if (!response.ok) {
        // If API call fails, revert the optimistic update
        setUser(user);
        throw new Error('Failed to update progress');
      }
      // The backend response could be used to re-sync state if needed
    } catch (error) {
      console.error(error);
      // Revert on error
      setUser(user);
    }
  };

  return (
    <AuthContext.Provider
      value={{
        user,
        token,
        login,
        logout,
        isAuthenticated: !!token,
        toggleQuestionProgress,
        isLoading,
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
};
